<template>
  <div class="move">
  <input id="file-upload" ref="upload" type="file" style="display: none" @change="onChangeUpload" />
  <h3 v-if="!this.isSample">Move {{this.moveIndex + 1}}</h3>
  <h3 v-else>{{this.name}}</h3>
  <form class="gender">
  <fieldset>
      <div>
          <input type="radio" id="slow" name="drone" @click="changeToMale()" checked/>
          <label for="slow">Male</label>
      </div>
      <div>
          <input type="radio" id="normal" name="drone" @click="changeToFemale()"/>
          <label for="normal">Female</label>
      </div>
  </fieldset>
</form>
  <Dragglable v-model="this.cards" :list="this.cards">
      <Card  v-for="(card, index) in this.cards"
        :key="index"
        :index="index"
        :id="'card-' + index"
        :title="card.title"
        :type="card.type"
        :initialized="card.initialized"
        :expended="card.expended"
        :direction="card.direction"
        :height="card.height"
        :weighted="card.weighted"
        :unweighted="card.unweighted"
        :leaning="card.leaning"
        :timeDelay="card.delay"
        :removeCard="removeCard"
        :expendCard="expendCard"
        :enableTyping="enableTyping"
        :submitChanges="submitChanges"
        :isSample="isSample"
        :moveIndex="moveIndex"
        :copyCard="copyCard"
        :poseCard="poseCard"
        :updatePlayFrame="updatePlayFrame"
        :addThisCard="addThisCard"
        :sampleMove="sampleMove"
        :updatePlayFrameSample="updatePlayFrameSample"
        :gender="gender"
      >
      </Card>
  </Dragglable>
  <Card v-if="this.inserting"
    type="card"
    :initialized="false"
    :onAddingNewSubmit="this.addCard"
    :onAddingNewCancel="this.onClickAddNewCancel"
    :enableTyping="this.enableTyping"
  ></Card>
  <Card v-else @click.native="onClickAddNew()"></Card>
    <button
          class="side-button"
          style="border: 5px; background-color: #1E90FF"
          @click="testingThisMove()"
    >move</button>
    <button
          class="side-button"
          style="border: 5px; background-color: #DC143C"
          @click="removeThisMove()"
          v-if="!this.isSample"
    >delete</button>
    <button
          class="side-button"
          style="border: 5px; background-color: #FFD700; color: black"
          @click="pasteMoveHere(moveIndex)"
          v-if="!this.isSample"
    >paste</button>
    <button
          class="side-button"
          style="border: 5px; background-color: green"
          @click="addThisMove()"
          v-if="this.isSample"
    >add</button>
    <br>
  </div>
</template>

<script>
import Card from './Card.vue'
import SidePanel from './SidePanel.vue'
import Index from './Index.vue'
import { validateCard, matchFrameIndex } from '../helper/cardHelper'
import preloadData from '../../static/preload.json'
import { isCookieEnabled, getCookie, setCookie } from 'tiny-cookie'
import axios from 'axios'
import Dragglable from 'vuedraggable'

export default {
  components: {
    'Card': Card,  //list of Cards make up one Move
    'SidePanel': SidePanel, //list of Moves make up one SidePanel
    'Dragglable': Dragglable, //(to make the moves draggable)
  },
  props: [
    // a list of cards
    'name',
    'mCards', //which poses make up the move?
    'moveIndex', //what is this move's index in the list of Moves?
    'handleOneMove', //function to play one move
    'removeOneMove', //function to remove one move from the list
    'addMove', //function to add one move to the list
    'isSample', //is this move a standard Tango move?
    'findComplement',
    'copyCard',
    'pasteMoveHere',
    'addCard',
    'updatePlayFrame',
    'sampleMove',
    'updatePlayFrameSample',
    'changedCard',
  ],
  data() {
    return {
      inserting: false,
      enableTyping: false,
      playing: false,
      currentCard: 0,
      inserting: false,
      gender: 'male',
      femaleCards: [],
      maleCards: Array.from(this.mCards, x => x),
      cards: [],
    }
  },
  watch: {
    cards: function (val) {
      this.$forceUpdate()
      if (this.gender === 'male') {
        this.maleCards = Array.from(this.cards, x => x)
      }
      else {
        this.femaleCards = Array.from(this.cards, x => x)
      }
    }
  },
  updated() {
    console.log('updated', this.cards)
  },
  mounted () {
    // see if cookie is enabled
    // console.log('this.cards', this.cards)
    // console.log('this.listOfCards', this.listOfCards)
    try {
      if (isCookieEnabled()) {
        let previousData = JSON.parse(getCookie('this.cards'))
        if (previousData instanceof Array) {
          // set data
          this.cards = previousData
        }
      }
    } catch (err) {
      console.log(err)
    }
    for (let i = 0; i < this.maleCards.length; i++) {
      let cCard = JSON.parse(JSON.stringify(this.findComplement(this.maleCards[i])))
      this.femaleCards.push(cCard)
    }
    this.cards = this.maleCards
  },

  beforeUpdate () {
    // update to cookie
    //this.$emit('add-move', this.cards)
    try {
      if (isCookieEnabled()) {
        setCookie('cards', JSON.stringify(this.cards))
      }
    } catch (err) {
      console.log(err)
    }
  },
  methods: {
    // add a new card element to the list
    poseCard (index) {
      this.$emit('update-play-frame', matchFrameIndex(this.cards[index]))
    },
    addThisCard (title, direction, height, weighted, unweighted, leaning, delay) {
      console.log('moveIndex', this.moveIndex)
      addCard(
        title,
        direction,
        height,
        weighted,
        unweighted,
        leaning,
        delay,
        this.moveIndex)
    },
    changeToMale() {
      this.gender = 'male'
      this.cards = this.maleCards
      this.changedCard(this.moveIndex, this.cards)
      console.log('male', this.cards)
    },
    changeToFemale() {
      this.gender = 'female'
      this.cards = this.femaleCards
      this.changedCard(this.moveIndex, this.cards)
      console.log('female', this.cards)
    },
    testingThisMove () {
      this.handleOneMove(this.cards)
    },
    removeThisMove () {
      console.log('the first one is', this.cards[0].title)
      this.removeOneMove(this.moveIndex)
    },
    addThisMove () {
      let move = {
        cards: Array.from(this.cards, x => x),
        isSample: 'false',
      }
      this.addMove(move)
    },
    insertCardAfter (index, after) {
      if (index !== after && index >= 0 && index <= this.cards.length - 1 && after >= 0 && after <= this.cards.length - 1) {
        let element = this.cards[index]
        // remove item index
        this.cards.splice(index, 1)
        let insertAfter = after
        // check if index is before 'after'
        if (index < after) {
          insertAfter -= 1
        }
        // insert element back
        this.cards.splice(insertAfter + 1, 0, element)
      }
    },
    expendCard (index) {
      if (!this.playing && index >= 0 && index < this.cards.length) {
        for (let i = 0; i < this.cards.length; i++) {
          this.cards[i].expended = false
        }
        this.cards[index].expended = true
        //console.log('here', this.cards)
        console.log('expended', index)
        this.playFrame = matchFrameIndex(this.cards[index])
        this.cPlayFrame = this.findComplement(this.cards[index])
      }
    },
    // remove a card by index
    removeCard (index) {
      if (index >= 0 && index <= this.cards.length - 1) {
        this.cards.splice(index, 1)
        this.playFrame = 0
      }
    },
    onClickAddNew () {
      if (!this.playing) {
        this.inserting = true
      }
    },
    onClickAddNewCancel () {
      this.inserting = false
    },
    // card update functions
    submitChanges (index, changes) {
      if (!this.playing) {
        let c = this.cards[index]
        c.direction = changes.direction
        c.height = changes.height
        c.weighted = changes.weighted
        c.unweighted = changes.unweighted
        c.leaning = changes.leaning
        c.delay = changes.delay
        if (validateCard(c)) {
          this.cards[index] = c
          return true
        }
      }
      return false
    },
    onChangeUpload (event) {
      let files = event.target.files || event.dataTransfer.files
      if (files.length === 1) {
        let reader = new FileReader()
        reader.onload = (e) => {
          let uri = e.target.result
          axios.get(uri).then((res) => {
            let data = res.data
            if (data instanceof Array) {
              this.cards = data
            }
          }).catch((err) => {
            console.log(err)
          })
        }
        reader.readAsDataURL(files[0])
      } else {
        console.log('length of files is not 1')
      }
      // clear the value here otherwise it won't work
      // the next time we upload the same file.
      document.getElementById('file-upload').value = ''
    },
  }
}
</script>

<style scoped>
.move {
  top: 1%;
  width: 100%;
  position: relative;
}
.side-button {
  color: white;
  position: relative;
  border-radius: 3px;
  box-shadow: 0px 0px 1px 1px gray;
  cursor: pointer;
}
.gender {
  width: 98%;
}
</style>



